
.header {
  display: flex;
  align-items: center;
  height: 120px;
  background-color: #add8e6;
  justify-content: space-between;
}
.header > h1 {
  margin: 0px 40px;
  font-size: 50px;
  font-weight: bold;
  color: #333333;
}
.userIcon {
  color: #000000;
  padding-right: 50px;
  font-size: 40px;
}
.userIcon:hover {
  color: #0cb449;
}
.container {
  width: 100%;
}







.morningEntries {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  border: 2px solid;
  border-collapse: collapse;
  width: 100%;
  box-shadow: 5px 5px 8px rgb(126, 123, 123);
}
.MorningHeading {
  padding: 5px;
  background-color: rgb(240, 158, 219);
  color: white;
  border-bottom: 1px black solid;
}
.MorningData td{
      text-align: center;
      vertical-align: middle;
}
.MorningData:hover {
  background-color: aquamarine;
  box-shadow: 5px 5px 8px rgb(126, 123, 123);
}
.MorningHeading:hover {
  box-shadow: 5px 5px 8px rgb(126, 123, 123);
}
.green-row {
  background-color: green;
}
.EntriesRow{
  display: flex;
  width: 100%;
}
.DateSelector{
  display: inline-flex;
  border: 2px solid;
  border-radius: 8px;
  background-color: rgba(117, 33, 196, 0.493);
  justify-content: center;
  align-items: center;
}
.DateSelector input{
  border-radius: 7px;
  margin: 2px;
  padding: 3px;
  background-color: rgba(255, 255, 255, 0.678);
}
.DateSelector button{
  height: 30px;
  margin-left: 8px;
  margin-top: 2px;
  color: white;
  background-color: rgb(145, 38, 207);
  width: 50px;

}
.main {
  width: 100%;
}
.container1 {
  display: flex;
  width: 100%;
  flex-direction: column;
}





/* CSS file */
.balance-table-container {
  font-family: Arial, sans-serif;
}

.balance-table {
  border-collapse: collapse;
  width: 100%;
}

.balance-table th,
.balance-table td {
  border: 1px solid #dddddd;
  text-align: left;
  padding: 8px;
}

.balance-table tr:nth-child(even) {
  background-color: #f2f2f2;
}

.balance-table thead {
  background-color: skyblue;
  color: white;
}

.balance-table .MorningData {
  background-color: #e6f7ff; /* Light blue */
}


.login-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.login-form {
  border: 1px solid #ccc;
  padding: 20px;
  border-radius: 5px;
}

.input {
  margin-bottom: 10px;
}

.login-options {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
}

.icon {
  font-size: 24px;
  cursor: pointer;
}

/* Style for register page */
.register-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.register-form {
  border: 1px solid #ccc;
  padding: 20px;
  border-radius: 5px;
}








/*userIcon*/

.userInfoModal {
  position: absolute;
  right: 250px;
  transform: translateX(calc(100% + 10px));
  z-index: 2;
  background-color: #f0f0f0; /* Grey background color */
  border: 1px solid #ccc; /* Grey border */
  padding: 10px;
  border-radius: 5px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); /* Shadow effect */
}

.userInfoContent {
  position: relative;
  text-align: center;
}

.userInfoTable {
  width: 200px;
}

.tableLabel {
  font-weight: bold;
  padding-right: 10px;
}

.tableData {
  padding-left: 10px;
  font-size: 1rem;
}

.logoutButton {
  margin-top: 10px;
  background-color: #f44336; /* Red color for logout button */
  color: #fff; /* White text color */
  border: none;
  padding: 5px 10px;
  border-radius: 3px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.logoutButton:hover {
  background-color: #d32f2f; /* Darker red on hover */
}

.profileImage{
  width: 80%;
  border: 1px solid orange;
  border-radius:50%;
}










