.Entries{
    display: grid;
    width: 100%;
    grid-template-columns: 1fr 4fr;
}
.EntriesTable{
    display: flex;
    justify-content: space-around;
    flex:1fr;
    gap: 10px;
    padding: 10px;
}
.EntriesTable div{
    width: 100%;
}
.Table1{
   display: grid;
   grid-template-columns: 1fr 1fr;
   gap: 20px;
}
.Table1c{
    border: 2px solid;
}
.Header{
   display: flex;
   flex-direction: row;
   justify-content: space-around;
   border: 1px solid;
   color: rgb(5, 5, 5);
   background-color: #f3bc74;
}
.formTable{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    align-items: center;
    justify-content: center;
   align-content: center;
   gap: 1px;
}
.Rows{
    padding: 4px;
    background-color: #ecc695;
}
.formTable input{
    margin-top: 5px;
    min-width: 100px;
    border-radius: 4px;
    
}
@media (max-width: 768px) {
    .Table1{
        grid-template-columns: 1fr;
     } 
}